html{
  margin:0;
  padding:0;
  min-height: 100%;
  background-size:cover;
  background: -webkit-gradient(linear, left top, right bottom, from(blue), to(pink));
  background: -webkit-linear-gradient(top left, blue, pink);
  background: -o-linear-gradient(top left, blue, pink);
  background: linear-gradient(to bottom right, blue, pink);
  background-repeat: no-repeat;
}




.container .wrapper{
  
  margin:auto;
  width: 50%;
  text-align: center;
}


